@font-face {
  font-family: Avenir;
  src: url("Avenir.8bcf8c72.ttc"), url("Avenir-Book.657c3568.ttf"), url("Avenir-Heavy.01d9ab74.ttf");
}

html {
  --text-color: #3d4e61;
  --border-color: #e1e7ed;
  font-family: Avenir, sans-serif;
}

body {
  margin: 0;
}

.form {
  margin: 20px 8px;
}

.form__input {
  box-sizing: border-box;
  border: 1px solid var(--border-color);
  color: var(--text-color);
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  border-radius: 4px;
  width: 100%;
  font-family: inherit;
  box-shadow: 0 1px 8px #3d4e611a;
}

.form__input::placeholder {
  color: var(--text-color);
}

.form__input:hover {
  box-shadow: 0 4px 4px #00000040;
}

.form__input--large {
  background-position: 25px 24px;
  background-size: 19px;
  padding: 23px 62px;
  font-size: 16px;
}

.form__input--large:focus {
  text-shadow: 0 4px 4px #00000040;
  background: url("Search.ccd3c07b.svg") 25px 24px / 19px no-repeat, linear-gradient(#fff 0%, #f6f6f7 100%);
  outline: none;
  font-weight: 900;
  box-shadow: 0 4px 4px #00000040;
}

.form__input--small {
  background-position: 12px 14px;
  background-size: 11px;
  padding: 10px 33px;
  font-size: 14px;
}

.form__input--small:focus {
  background: url("Search.ccd3c07b.svg") 12px 14px / 11px no-repeat, linear-gradient(#fff 0%, #f6f6f7 100%);
  outline: none;
  font-weight: 900;
  box-shadow: 0 4px 4px #00000040;
}

